.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 100000000; }
  .modal.closed {
    display: none; }
  .modal__wrapper {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
    padding: 0 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.5);
    animation: showModal 0.7s forwards;
    width: 40%;
    min-height: 40%;
    background-color: #ffffff;
    box-shadow: 0 0 3px #ffffff; }
    @media (max-width: 1199px) {
      .modal__wrapper {
        padding: 0 35px; } }
    @media (max-width: 767px) {
      .modal__wrapper {
        max-width: 767px;
        min-width: 320px;
        padding: 0 10px; } }
  .modal__close {
    font-family: "Montserrat_Med";
    align-self: flex-end;
    border: none;
    padding: 0.5rem;
    margin: 0.5rem;
    cursor: pointer;
    color: #D4B07A;
    background-color: transparent; }
    @media (min-width: 767px) {
      .modal__close:hover, .modal__close:focus {
        transition: color 0.3s ease;
        color: #D4B07A; }
      .modal__close:active {
        transition: opacity 0.3s ease;
        opacity: 0.7; } }

@keyframes showModal {
  0% {
    transform: translate(-50%, -50%) scale(0.5); }
  100% {
    transform: translate(-50%, -50%) scale(1); } }
